export default {
	props: {
		border: {
			type: String,
			default: '1'
		},
		borderColor: {
			type: String,
			default: '#d0dee5'
		},
		isCheck: {
			type: Boolean,
			default: false
		}
	},
	provide() {
		return {
			table: this
		};
	},
	data() {
		return {};
	},
	created() {
		this.childrens = [];
		this.index = 0;
	},
	methods: {
		fire(e, index, len) {
			let childrens = this.childrens;
			console.log(childrens);
			// 全选
			if (index === 0) {
				childrens.map((vm, index) => {
					vm.checkboxData.checked = e;
					return vm;
				});
			} else {
				let isAll = childrens.find((n, ids) => ids !== 0 && !n.checkboxData.checked);
				childrens[0].checkboxData.checked = isAll ? false : true;
			}

			let fireArr = [];
			for (let i = 0; i < childrens.length; i++) {
				if (childrens[i].checkboxData.checked && i !== 0) {
					fireArr.push(childrens[i].checkboxData.value - 1);
				}
			}
			this.$emit('change', {
				detail: fireArr
			});
		}
	}
};
